  width: 180px;
  background-color: red;
  color: white;
  margin-top: 10px;
  position: absolute;
  bottom: -20px;
  left: 0;
  padding-left: 8px;
  border-radius: 3px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.error-text::before {
  content: "";
  border-color: transparent transparent red transparent;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 8px 8px 7px;
  position: absolute;
  left: 20px;
  top: -8px;
}

